<template>
	<div class="HomeDetails">
		<div class="content" ref="container" :style="scrollHeight">
			<div class="home-header">
				<div style="display: table-cell;
	    vertical-align: middle;text-align: center;">
					<h2 style="color: #B9862F;margin: 0;">{{balance.toFixed(6)}} {{title}}</h2>
					<!--<p style="color: #CEBC98;margin: 0;">≈{{totalMoney.toFixed(4)}}CNY</p>-->
				</div>
			</div>
			<div style="padding-left: 20px;height: 30px;background: #f5f5f5;line-height: 30px;">
				交易记录
			</div>
			
			<div style="margin-bottom: 60px;">
			<mu-load-more @refresh="refresh" :loaded-all="loadAll" :refreshing="refreshing" :loading="loading" @load="load">	
			<div v-for="item,index in recordList" :key="index" style="padding:10px 20px;border-bottom: 1px solid #e5e5e5;padding-bottom: 8px;background: white;">
				<p>时间 ：{{item.created}}<span style="float: right;color: #6A8DC2;" v-if="item.status==0">待审核</span>
					<span style="float: right;color: #E3C99B;" v-if="item.status==1">已成功</span>
					<span style="float: right;color: red;" v-if="item.status==2">已失败</span>
				</p>
				<p class="margin-top-8">类型 ：{{item.type}} </p>
				<p class="margin-top-8">数量 ：<span v-if="Number(item.num)<0" style="color: red;">{{Number(item.num).toFixed(2)}}</span>
					<span v-if="Number(item.num)>0" style="color: green;">{{Number(item.num).toFixed(2)}}</span>
				</p>
				<!--<p class="margin-top-8" style="word-wrap:break-word;word-break:break-all;overflow:hidden">地址 ：{{item.to}}</p>-->
			</div>
			<!--<div style="padding:10px 20px;border-bottom: 1px solid #e5e5e5;padding-bottom: 8px;">
				<p>转账时间 ：2018/01/01/10:23 <span style="float: right;color: #B9862F;">已成功</span></p>
				<p class="margin-top-8">转账类型 ：BTC </p>
				<p class="margin-top-8">转账金额 ：22.566</p>
				<p class="margin-top-8">转账地址 ：22.56666666</p>
			</div>-->
			</mu-load-more>
			<p v-if="recordList.length==0" style="text-align: center;">暂无数据...</p>
			<p style="text-align: center;" v-show="page>1&&loadAll">没有更多数据啦~~</p>
			</div>
		</div>
		<mu-container style="display: flex;position: fixed;z-index: 100;bottom: 0px;padding: 0;height: 55px;">
		  <mu-button @click="goTransferAccounts" color="#295DF1" style="border-radius: 0;width: 50%;height: 55px;">转账</mu-button>
		  <mu-button @click="goReceivables" flat color="#295DF1" style="background: white;border: 1px solid #295DF1;border-radius: 0;width: 50%;height: 55px;">收款</mu-button>
		</mu-container>
	</div>
</template>

<script>
let web3 = null;
export default {
  name: 'HomeDetails',
  data(){
  	return{
  		page:1,
  		recordList:[],
  		loading:false,
  		refreshing:false,
  		loadAll:false,
  		balance:0,
  		totalMoney:0,
  		title:''
  	}
  },
  methods:{
  	goTransferAccounts(){
  		this.$router.push('/transferAccounts')
  	},
  	goReceivables(){
//		this.$router.push('/receivables');
  	},
  	//获取币种资产记录
  	getRecord(){
      let _this = this;
      _this.$http.get(_this.$lib.url + 'wallet/assets?coins_type='+_this.$store.state.coinType+'&wallet_id='+localStorage.getItem('walletId')+'&page='+_this.page).then(res => {
			console.log(res);
			if (res.status == 0){
				_this.refreshing = false;
				const _list = res.data.data;
		        _this.recordList = [...this.recordList, ..._list];
		        for(let i in  _this.recordList){
		        	for(let j in  _this.$lib.coinsList){
		        		if(_this.recordList[i].type == j){
		        			_this.recordList[i].typeName=_this.$lib.coinsList[j]
		        		}
		        	}
		        }
		        if(_list.length < 10){
		            /* 所有数据加载完毕 */
		           	_this.loading = false;
					_this.loadAll = true;
		            return;
		        }
			}
		}).catch(err => {})
    },
    //转账后重新获取余额
    geyWalletInfo() {
		let _this = this;
		let data = {
			id: localStorage.getItem('walletId')
		}
		_this.$http.post(_this.$lib.url + 'wallet/info', _this.$qs.stringify(data)).then(res => {
			console.log(res);
			if(res.status == 0) {
				_this.walletInfo = res.data;
				_this.walletCoinsList = res.data.walletInfo;
				for(let i = 0; i < _this.walletCoinsList.length; i++) {
					if(this.title == _this.walletCoinsList[i].name) {
						_this.balance = _this.walletCoinsList[i].count;
					}
				}
			}
		}).catch(err => {})
	},
    //下拉刷新
	refresh() {
		this.refreshing = true;
		this.loadAll = false;
		this.$refs.container.scrollTop = 0;
		setTimeout(() => {
			this.page=1;
			this.recordList=[];
			this.getRecord();
		},1000)
	},
	//加载更多
	load() {
		this.loading = true;
		this.page++;
		setTimeout(() => {
			this.loading = false;//不设置的话第二次上拉加载不会调用load方法
			this.getRecord();
		},1000)
	}
  },
  computed:{
  	scrollHeight(){
		return "height:"+((window.screen.height)-56)+"px";
	},
  }, 
  mounted(){
  	this.title = this.$store.state.homeDetailsTitle;
  	this.chooseWallet = JSON.parse(localStorage.getItem('chooseWallet'))
  	web3 = new Web3(new Web3.providers.HttpProvider("https://mainnet.infura.io/v3/b7f93674854b43fea9d265249ba24c2d"));
  	this.balance = web3.eth.getBalance(this.chooseWallet.address);
//	this.page=1;
//	this.recordList=[];
//	this.getRecord();
  },
  watch:{
   '$route' (to,from){
   	 if(to.path == '/homeDetails'&&(from.path == '/home'||from.path == '/transferAccounts')){
   	 	this.balance = Number(this.$store.state.balance);
	  	this.totalMoney = this.balance*Number(this.$store.state.rate);
	  	this.title = this.$store.state.homeDetailsTitle;
	  	this.page=1;
	  	this.recordList=[];
	  	this.getRecord();
   	 }
   }
  },
  created(){
	
  }
 }
</script>

<style scoped>
.HomeDetails{

}
.home-header{
	position: relative;
	display: table;
	height: 100px;
	width: 100%;
}

</style>


